@import "main-bx";

.header {
    &__top {
        padding: 13px 0 10px;
        border-bottom: 1px solid #797979;

        @include below(768px) {
            padding: 0;
            border: 0;
        }

        &-inner {
            position: relative;
        }
    }

    &__logo {
        @include below(768px) {
            position: absolute;
            top: 8px;
            right: 15px;
            width: 100%;
            max-width: 44.17px;
            height: 50px;
        }
        img {
            max-width: 150px;

            @include below(768px) {
                display: none;
                max-width: 100%;
            }
        }

        &_hide {
            display: none;
            @include below(768px) {
                display: block !important;
            }
        }
    }
    &__center {
        padding: 2px 0 10px;
        &-nav {
            display: flex;
            align-items: center;
        }

        &-item {
            position: relative;
            &:not(:last-child) {
                padding-right: 8px;
                margin-right: 8px;
                
                &::after {
                    content: "";
                    position: absolute;
                    top: 12px;
                    right: 0;
                    width: 1px;
                    height: 10px;
                    background-color: #4f4f4f;
                }
            }
            a {
                @include fontProperty(400, 13px, 16px, #4F4F4F);
                transition: $trns;
                padding: 8px 0;
                display: block;
        
                &:hover {
                    opacity: .8;
                }
            }
        }

        @include below(768px) {
            display: none;
        }
    }

    &__nav {
        display: flex;
        align-items: center;
        background-color: $color_green;
        padding: 5px 0;
        &-item {
            a {
                @include fontProperty(700, 17px, 20px, $white);
                text-transform: uppercase;
                margin: 0 11px;
                display: block;
                transition: $trns;
                border-bottom: 1px solid transparent;

                &:hover {
                    border-color: $white;
                }
            }
        }
        @include below(768px) {
            display: none;
        }
    }

    &__bottom {
        position: relative;
        margin-bottom: 20px;

        @include below(768px) {
            overflow: hidden;
        }

        &-pan {
            position: absolute;
            bottom: -62px;
            z-index: 100;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 9px 11px;
            background-color: #fff;

            @include below(768px) {
                position: relative;
                bottom: auto;
                flex-direction: column;
                align-items: flex-start;
                background-color: transparent;
                padding-top: 16px;
            }
        }

        &-left {
            @include below(768px) {
                padding-bottom: 8px;
            }
        }

        &-text {
            @include fontProperty(400, 17px, 20px, $black);
            @include below(768px) {
                font-size: 16px;
                color: $white;
            }
            &_bold {
                text-transform: uppercase;
                margin-bottom: 4px;
                @include below(768px) {
                    font-weight: 700;
                    font-size: 24px;
                    // margin-bottom: 0;
                    text-transform: none;
                }
            }

            &_hidden {
                display: none; 

                @include below(768px) {
                    display: inline-block;
                }
            }
        }

        &::before {
            position: absolute;
            top: 0;
            z-index: 20;
            left: -30px;
            width: 280px;
            height: 67px;
            background: #4BA82E;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
            transform: skew(-10deg);
            @include below(768px) {
                content: "";
            }
        }

        &::after {
            position: absolute;
            bottom: 3px;
            z-index: 10;
            left: -30px;
            width: 273px;
            height: 67px;
            background: #3B8723;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
            transform: skew(-10deg);
            @include below(768px) {
                content: "";
            }
        }
    }

    &__tel {
        @include fontProperty(400, 16px, 19px, $color_green);
        text-transform: uppercase;
        display: block;
        @include below(768px) {
            color: #fff;
            background-color: #4BA82E;
            padding: 2px 42px 2px 8px;
            margin-top: 7px;
            
        }

        span {
            border-bottom: 1px solid transparent;
            transition: $trns;
        }

        &:hover {
            span {
                border-color: $color_green;
            }
        }
        svg {
            width: 14px;
            height: 14px;
            margin-right: 6px;
            fill: currentColor;
        }
    }
}