@import "main-bx";

.mfp-content {
    .modal {
        display: block;
    }
}

.modal {
    display: none;
    position: relative;
    width: 100%;
    max-width: 440px;
    background-color: $white;

    margin: 0 auto;
    padding: 30px 50px;

    &__title {
        font-weight: 700;
        font-size: 28px;
        line-height: 34px;
        color: #2F3538;
        padding-bottom: 20px;
        text-align: center;
    }

    &__text {
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        color: #A7AEB4;
        margin-bottom: 30px;
        padding: 0 20px;
    }

    .form__btn {
        width: 100%;
    }
}