@import "main-bx";

.footer {
    &__inner {
        padding: 28px 0;
    }
    &__article {

        &-head {
            padding: 20px 0;
            cursor: pointer;
        }
        &-btn {
            display: flex;
            align-items: center;
            @include fontProperty(400, 16px, 19px, #4BA82E);
            img {
                width: 10px;
                height: 7px;
                margin-left: 6px;
                transition: $trns;
            }

            span {
                text-decoration-line: underline;
            }

            &.active {
                img {
                    transform: rotate(180deg)
                }
            }
        }
        &-body {
            max-height: 0;
            overflow: hidden;
            transition: $trns;
            &.active {
                max-height: 1000rem;
                overflow: visible;
                padding-bottom: 65px;
            }
            p {
                @include fontProperty(400, 14px, 17px, #4F4F4F);
                text-align: justify;
                &:not(:last-child) {
                    margin-bottom: 15px;
                }

                @include below(768px) {
                    font-size: 9px;
                    line-height: 11px;
                }
            }
        }
    }

    &__bottom {
        position: relative;
        padding: 40px 0;
        background-color: #FAFAFA;
        &-wrap {
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: relative;
            @include below(768px) {
                flex-direction: column;
            }
        }
    }

    &__text {
        @include fontProperty(400, 16px, 19px, #000000);
    
        &_info {
            color: #4BA82E;
            text-decoration: underline;

            @include below(768px) {
                margin-top: 20px;
            }
        }
    }

    &__top {
        position: absolute;
        top: -50px;
        left: 0;
        right: 0;
        margin: 0 auto;
        width: 50px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #000000;

        cursor: pointer;
    }
}