@import "main-bx";

.intro {
    position: relative;
    overflow: hidden;
    @include below(768px) {
        display: none;
    }

    &__over {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }

    &__img {
        img {
            display: block;
            min-height: 700px;
        }
    }

    &__wrap {
        height: 100%;
        position: relative;
        padding-top: 60px;
    }

    &__title {
        @include fontProperty(700, 42px, 51px, $black);
        text-transform: uppercase;
        margin-bottom: 30px;

        @include below(1024px) {
            font-size: 35px;
            line-height: 44px;
        }
    }

    &__text {
        @include fontProperty(400, 34px, 41px, $black);
        text-transform: uppercase;

        @include below(1024px) {
            font-size: 28px;
            line-height: 34px;
        }
    }

    &__car {
        // position: absolute;
        &-item {

        }

        &-die {
            position: absolute;
            bottom: 280px;
            padding: 15px 15px 15px 25px;
            background-color: #fff;
        
            &_left {
                left: 0;

                @include below(1200px)  {
                    left: 15px;
                }
            }

            &_center {
                left: 480px;

                @include below(1200px) {
                    left: 400px;
                }

                @include below(1024px) {
                    left: 350px;
                }

                @include below(810px) {
                    left: 300px;
                }
            }

            &_right {
                bottom: 320px;
                right: -50px;

                @include below(1200px)  {
                    right: 15px;
                }
            }
        }

        &-key {
            @include fontProperty(400, 24px, 29px, $black);
            text-transform: uppercase;
            margin-bottom: 5px;

            
        }

        &-val {
            @include fontProperty(700, 36px, 100%, $color_green);
            text-transform: uppercase;

            sup {
                font-size: 20px;
            }

            @include below(1024px) {
                font-size: 28px;
            }
        }

        &-img {
            position: absolute;
            width: 100%;
            bottom: 0;
            &_left {
                max-width: 533px;
                left: -170px;
                z-index: 1;
                @include below(1200px)  {
                    left: -20px;
                }

                @include below(1024px) {
                    max-width: 433px;
                }

                @include below(850px) {
                    max-width: 380px;
                }
            }

            &_center {
                max-width: 625px;
                right: 230px;
                z-index: 10;

                @include below(1024px) {
                    max-width: 525px;
                }

                @include below(850px) {
                    max-width: 480px;
                    right: 130px;
                }
            }

            &_right {
                max-width: 625px;
                right: -170px;
                z-index: 2;
                @include below(1200px)  {
                    right: -50px;
                }

                @include below(1024px) {
                    max-width: 525px;
                }

                @include below(850px) {
                    max-width: 460px;
                }
            }
        }
    }
}