@import "main-bx";

.form {
    fieldset {
        padding: 0;
        margin: 0;
        border: 0;
    }

    &__label {
        input {
            @include fontProperty(400, 16px, 19px, $black);
            border: 1px solid #000000;
            border-radius: 3px;
            padding: 12px 25px;
        }
    
        select {
            width: 100%;
        }

        .select2-container {
            width: 100% !important;
        }

       
        .select2-container--default .select2-selection--single,
        select {
            border-radius: 0;
            border: 1px solid #DEDEDE;
            transition: $trns;
            height: auto;
            &:hover {
                border-color: $color_green;

                .select2-selection__rendered {
                    color: $color_green
                }
            }
        }

        .select2-container--default .select2-selection--single .select2-selection__rendered {
            @include fontProperty(400, 14px);
            color: #4F4F4F;
        }

        .select2-container--default .select2-selection--single .select2-selection__arrow {
            background-image: url(../img/svg/down.svg);
            background-repeat: no-repeat;
            width: 10px;
            height: 10px;
            top: 13px;
            right: 7px;
            b {
                display: none;
            }
        }

        [aria-expanded="true"] {
            .select2-selection__arrow {
                transform: rotate(-180deg);
                top: 7px !important;
            }
        }

        &.error {
            input {
                border-color: #ff0000 !important;
                color: #ff0000 !important;
            }
        }

    }

    &__check {
        display: flex;
        align-items: center;
        cursor: pointer;
        input:checked + .form__check-box {
            border-color: $color_green;

            img {
                opacity: 1;
            }
        }

        &-box {
            width: 100%;
            max-width: 20px;
            height: 20px;

            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid #A7AEB4;
            margin-right: 10px;
            transition: $trns;
            img {
                width: 10px;
                height: 10px;
                opacity: 0;
                transition: $trns;
            }
        }

        &-text {
            @include fontProperty(400, 12px, 14px, $black);

            a {
                color: $black;
                text-decoration: underline;
                transition: $trns;
                &:hover {
                    opacity: 0.4;
                }
            }
        }

        &.error {
            .form__check-box {
                border-color: #ff0000 !important;
            }
            .form__check-text {
                color: #ff0000 !important;
            }
        }
    }
} 


.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
    background-color: $color_green !important;
}