@import "main-bx";

.model-nav {

    &__inner {
        padding-top: 70px;
        padding-bottom: 30px;

        @include below(768px) {
            padding-top: 40px;
            padding-bottom: 20px;
        }
    }

    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 2px solid #A7AEB4;

        @include below(900px) {
            flex-wrap: wrap;
        }

        @include below(768px) {
            border: 0;
            margin-top: -10px;
        }
    }

    &__item {
        @include below(900px) {
            width: 33.3333%;
            text-align: center;
        }

        @include below(768px) {
            width: 48%;
            border-bottom: 2px solid #A7AEB4;
            margin-top: 10px;
        }

        a {
            position: relative;
            display: block;
            @include fontProperty(700, 30px, 36px, $color_grey);
            letter-spacing: 0.05em;
            text-transform: uppercase;
            padding: 15px 0;
            transition: $trns;
            @include below(768px) {
                padding: 10px 0;
                font-size: 18px;
                line-height: 22px;
            }
            span {
                position: absolute;
                top: 0;
                left: 0;
                font-weight: normal;
                font-size: 10px;
                line-height: 18px;
                text-transform: none;
                @include below(768px) {
                    top: -5px;
                    
                    right: 0;
                    text-align: center;
                    
                }
            }
            
            &:hover {
                color: $color_green;
            }

            &::after {
                content: "";
                position: absolute;
                bottom: -2px;
                left: 0;
                width: 100%;
                height: 2px;
                background-color: $color_green;
                opacity: 0;
                transition: $trns;
            }

            &.active {
                color: $color_green;
                &::after {
                    opacity: 1;
                }
            }
        }
    }
}