@import "main-bx";

.store {
   
    &__inner {
        display: flex;
        padding-top: 40px;
        padding-bottom: 40px;

        @include below(900px) {
            flex-direction: column;
        }

        @include below(768px) {
            padding-bottom: 0;
        }
    }

    &__form {
        width: 100%;
        max-width: 420px;
        background-color: rgba(167, 174, 180, 0.2);
        padding: 40px;
        margin-right: 100px;

        @include below(768px) {
            max-width: 100%;
            margin-right: 0;
            padding: 20px;
        }

        &-title {
            @include fontProperty(700, 24px, 29px, $black);
            text-align: center;
            text-transform: uppercase;
            margin-bottom: 20px;
            padding: 0 50px;

            @include below(768px) {
                padding: 0;
            }
        }

        &-text {
            @include fontProperty(400, 18px, 157%, $black);
            text-align: center;
            margin-bottom: 30px;

            @include below(768px) {
                margin-bottom: 20px;
            }
        }
    }

    &__form-container {
        fieldset {
            display: flex;
            flex-direction: column;
        }

        input {
            width: 100%;
            margin-bottom: 20px;
        }

        button {
            margin-bottom: 20px;
        }

        .form__check-text {
            padding-right: 30px;
        }
    }

    &__content {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: -20px;
        padding-top: 54px;
        width: 100%;
        max-width: 550px;

        @include below(768px) {
            padding-top: 0;
            margin-top: -40px;
            padding-top: 30px;
        }
    }

    &__content-item {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 50%;

        @include below(768px) {
            width: 100%;
            margin-top: 40px;
        }
        p {
            @include fontProperty(400, 18px, 22px, $black);
            text-align: center;
        }

        img {
            width: 100%;
            max-width: 60px;
            height: 60px;
            margin-bottom: 25px;

            @include below(768px) {
                margin-bottom: 20px;
            }
        }
    }
}