@import "main-bx";


.stock {
    &__inner {
        width: 100%;
        max-width: 900px;
        margin: 0 auto; 
        margin-bottom: 80px;
        @include below(768px) {
            max-width: 100%;
            margin-bottom: 40px;
        }
    }

    &__title {
        @include fontProperty(700, 30px, 36px, $black);
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 24px;

        @include below(768px) {
            font-size: 20px;
            line-height: 24px;
            margin-bottom: 20px;
        }
    }

    &__from {
        margin-bottom: 24px;
        @include below(768px) {
            margin-bottom: 10px;
        }
        fieldset {
            display: flex;
            align-items: center;
            justify-content: space-between;

            @include below(768px) {
                flex-direction: column;
            }
        }

        .form__label {
            width: 24%;

            @include below(768px) {
                width: 100%;
                margin-bottom: 10px;
            }
        }
    }

    &__row {
        display: flex;
        background: #FAFAFA;
        border: 1px solid #DEDEDE;
        padding: 16px 20px 20px 0;

        &:not(:last-child) {
            margin-bottom: 20px;
        }

        @include below(768px) {
            flex-direction: column;
            padding: 12px;
        }
    }

    &__img {
        width: 100%;
        max-width: 286px;
        margin-right: 24px;
        padding-top: 25px;

        @include below(768px) {
            margin: 0;
            padding: 0;
            max-width: 100%;
            margin-bottom: 20px;
        }
    }

    &__model {
        margin-right: 60px;

        @include below(768px) {
            margin-right: 0;
            margin-bottom: 20px;
        }

        &-name {
            @include fontProperty(700, 16px, 19px, $black);
            margin-bottom: 3px;
            text-transform: uppercase;
        }

        &-tip {
            @include fontProperty(400, 14px, 17px, #828282);
            margin-bottom: 20px;
            letter-spacing: 0.005em;
            @include below(768px) {
                margin-bottom: 12px;
            }
        }

        &-list {
            li {
                display: flex;
                align-items: center;
                &:not(:last-child) {
                    margin-bottom: 8px;
                }
            }
        }

        &-key {
            width: 100%;
            min-width: 75px;
            max-width: 75px;
            margin-right: 48px;
            @include fontProperty(400, 12px, 14px, #828282);
            letter-spacing: 0.04em;
            white-space: nowrap;
        }

        &-val {
            width: 100%;
            max-width: 110px;
            @include fontProperty(400, 13px, 16px, #4F4F4F);
            letter-spacing: 0.04em;
            white-space: nowrap;
        }
    }

    &__contact {
        display: flex;
        flex-direction: column;
        flex: auto;
    }

    &__cost {
        @include fontProperty(400, 13px, 16px, #4F4F4F);
        letter-spacing: 0.04em;
        margin-bottom: 5px;

        strong {
            font-size: 22px;
        }

        sup {
            font-size: 10px;
            top: -1em;
        }
    }

    &__tel {
        @include fontProperty(400, 13px, 16px, #828282);
        letter-spacing: 0.04em;
        margin-bottom: 14px;
        a {
            @include fontProperty(700, 18px, 22px, #4F4F4F);
        }
    }

    &__btn {
        width: 100%;
        padding: 10px 13px;
        &.btn_green {
            margin-bottom: 6px;
        }
    }

    &__more {
        display: flex;
        justify-content: center;
        margin-top: 30px;
        &-btn {
            padding: 12px 60px;
        }
    }
}