@import "main-bx";
 

.btn {
    @include fontProperty(400, 16px, 19px);
    text-align: center;
    border-radius: 3px;
    transition: all .2s linear;
    cursor: pointer;
    padding: 13px;
    position: relative;
    @include below(768px) {
        width: 100%;
        justify-content: center;
    }

    &_green {
        color: #fff;
        background-color: #4BA82E;
        border: 1px solid #4BA82E;
        &:hover {
            background-color: #fff;
            color: #4BA82E;
            box-shadow: 0px 0px 30px rgba(75, 168, 46, .3);
        }

        &:active {
            color: #fff;
            background-color: #4BA82E;
        }
    }

    &_light {
        color: #4BA82E;
        background-color: #fff;
        border: 1px solid #4BA82E;
        &:hover {
            background-color: #4BA82E;
            color: #fff;
            box-shadow: 0px 0px 30px rgba(75, 168, 46, .3);
        }

        &:active {
            color: #4BA82E;
            background-color: #fff;
        }
    }

    &:disabled {
        cursor: no-drop;
        opacity: 0.7;
        span {
            opacity: 0;
        }
        .btn__spin {
            opacity: 1;
        }

        &:hover {
            background-color: #4BA82E;
        }
    }

    &__spin {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: 0 auto;
        opacity: 0;
    } 

}
