@import "main-bx";

.models {
   
    &__inner {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 0 30px;
        margin-top: -70px;
        margin-bottom: 80px;
        @include below(900px) {
            max-width: 768px;
        }

        @include below(768px) {
            margin-top: -30px;
            margin-bottom: 30px;
        }
    }

    &__item {
        width: 100%;
        max-width: 520px;
        min-height: 872px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-top: 70px;
        position: relative;
        @include below(1380px) {
            max-width: 48%;
        }

        @include below(900px) {
            max-width: 100%;
        }

        @include below(768px) {
            min-height: 100%;
            margin-top: 30px;
        }
    }

    &__img {
        height: 300px;
        margin-bottom: 38px;

        img {
            height: auto;
        }

        @include below(768px) {
            height: auto;
            margin-bottom: 0;
        }
    }

    &__txt {
        @include fontProperty(400, 14px, 17px, $color_green);
        text-transform: uppercase;
        &_pos-top {
            position: absolute;
            top: -18px;
            left: 0;
        }
        &_align-left {
            text-align: right;

            @include below(768px) {
                text-align: left;
                color: $black;
            }
        }
    }

    &__head {
        position: relative;
        margin-bottom: 40px;
        display: flex;
        justify-content: space-between;

        &-item {
            &_last {
                padding-right: 20px;

                @include below(768px) {
                    position: absolute;
                    top: 56px;
                }
            }
        }

        @include below(768px) {
            margin-bottom: 25px;
        }
    }

    &__name {
        @include fontProperty(700, 30px, 36px, $color_green);
        margin-bottom: 6px;
        text-transform: uppercase;

        @include below(768px) {
            font-size: 20px;
            line-height: 24px;
            color: $black;
        }
    }

    &__anchor {
        @include fontProperty(700, 18px, 22px, $color_green);
        display: flex;
        align-items: center;
        transition: $trns;
        margin-bottom: 10px;
        img {
            width: 12px;
            height: 12px;
            margin-left: 5px;
        }

        &:hover {
            color: #35871c;
        }

        @include below(768px) {
            font-size: 16px;
            line-height: 19px;
        }

        &_first {
            @include below(768px) {
                margin-bottom: 81px;
            }
        }

        &_second {
            @include below(768px) {
                margin-bottom: 100px;
            }
        }
        
    }

    &__call {
        @include fontProperty(400, 16px, 19px, $color_grey);
        border-bottom: 1px solid $color_grey;
    }

    &__gift {
        @include fontProperty(400, 18px, 22px, $color_green);
        
        text-align: right;

        @include below(768px) {
            text-align: left;
        }

        span {
            color: $black;
        }

        p {
            font-weight: 700;
            text-transform: uppercase;
        }

        sup {
            font-size: 8px;
            top: -7px;
        }
    }

    &__body {
        margin-bottom: auto;
        &-row {
            display: flex;
            justify-content: space-between;

        }
    }

    &__date {
        @include fontProperty(700, 22px, 133%, $color_green);
        text-transform: uppercase;
        margin-bottom: 14px;

        @include below(768px) {
            position: absolute;
            top: 280px;
            left: 0;

            font-size: 18px;
            line-height: 21px;
            padding-right: 50px;
        }
    }

    &__benefit {
        @include fontProperty(400, 14px, 17px, $color_green);
        text-transform: uppercase;
        margin-bottom: 18px;
        width: 50%;
        padding-left: 32px;
        position: relative;
        
        @include below(768px) {
            padding-left: 15px;
        }

        &:first-child {
            margin-right: 20px;
        }
        &_mr-null {
            margin-bottom: 0;
        }

        &_mr-15 {
            margin-bottom: 15px;
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            max-width: 20px;
            height: 20px;
            border-radius: 50%;
            background-color: $color_green;
            margin-right: 12px;

            @include below(768px) {
                max-width: 10px;
                height: 10px;
                margin-right: 5px;
            }
        }

        &-key {
            color: $black;
            margin-bottom: 5px;

            &_green {
                color: $color_green;
                margin-bottom: 0;
            }

        }
        &-val {
            font-weight: 700;
            font-size: 28px;
            line-height: 29px;

            @include below(768px) {
                font-size: 20px;
                line-height: 24px;
            }
        }

        sup {
            font-size: 8px;
            top: -14px;
        }
    }

    &__buttons {
        &-row {
            display: flex;
            align-items: center;
            justify-content: space-between;

            &:not(:only-child) {
                margin-bottom: 20px;
            }
        }
        &-half {
            width: 100%;
            max-width: 250px;
            white-space: nowrap;
            @include below(1380px) {
                max-width: 48%;
            }

            @include below(768px) {
                white-space: normal;
                padding: 13px 20px;
            }
        }

        &-full {
            width: 100%;
        }
    }
}