@import "main-bx";

.contacts {
    &__inner {
        padding: 80px 0 40px;

        @include below(768px) {
            padding: 40px 10px 30px;
        }
    }

    &__title {
        @include fontProperty(700, 34px, 42px, $black);
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 40px;

        @include below(768px) {
            font-size: 20px;
            line-height: 24px;
            margin-bottom: 0;
        }
    }

    &__d-flex {
        width: 100%;
        max-width: 860px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;

        @include below(768px) {
            flex-direction: column;
        }
    }

    &__item {
        display: flex;
        width: 100%;
        padding: 20px 0 25px;

        @include below(768px) {
            padding: 30px 0;
        }

        &:nth-child(1) {
            max-width: 250px;
            @include below(768px) {
                max-width: 100%;
            }
        }

        &:nth-child(2) {
            max-width: 300px;
            @include below(768px) {
                max-width: 100%;
            }
        }

        &:nth-child(3) {
            max-width: 300px;
            @include below(768px) {
                max-width: 100%;
            }
        }
        &:not(:last-child) {
            border-right: 1px dashed #A7AEB4;
            @include below(768px) {
                border-right: 0;
                border-bottom: 1px dashed #A7AEB4;
            }
        }

        &:not(:first-child) {
            padding-left: 20px;
            @include below(768px) {
                padding-left: 0;
            }
        }

        &-icon {
            width: 100%;
            min-height: 50px;
            max-width: 50px;
            height: 50px;
            margin-right: 20px;
        }

        &-title,
        &-txt {
            @include fontProperty(700, 18px, 22px, $black);
        }

        &-title {
            text-transform: uppercase;
            margin-bottom: 15px;
        }

        &-txt {
            font-weight: 400;
            span {
                display: block;
                color: $color_green;
                font-size: 16px;
            }
        }
    }
}