@import "main-bx";

.trade-in {
    @include below(768px) {
        display: none;
    }
    &__inner {
        position: relative;
        margin-bottom: 85px;
    }

    &__title {
        @include fontProperty(700, 34px, 42px, $black);
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 157px;
    }

    &__text {
        @include fontProperty(400, 16px, 19px, $black);
        text-align: center;
        margin-bottom: 30px;
    }

    &__img {
        width: 100%;
        max-width: 600px;
        margin: 0 auto;
        margin-bottom: 50px;
    }

    &__btn {
        display: flex;
        justify-content: center;

        .btn {
            padding: 12px 30px;
        }
    }

    &__item {
        position: absolute;
        width: 100%;
        top: 0;

        &-title {
            @include fontProperty(700, 18px, 22px, $black);
            text-transform: uppercase;
            margin-bottom: 10px;
            padding-left: 5px;
            border-left: 2px solid #4BA82E;
        }

        &-text {
            @include fontProperty(400, 14px, 17px, #A7AEB4);
            padding-left: 7px;
        }

        &_01 {
            top: 230px;
            left: 0;
            max-width: 260px;
        }

        &_02 {
            top: 130px;
            left: 300px;
            max-width: 180px;

            @include below(1180px) {
                left: 100px;
                top: 70px;
            }
        }

        &_03 {
            top: 90px;
            right: 300px;
            max-width: 320px;
            @include below(1180px) {
                right: 100px;
                top: 70px;
            }
        }

        &_04 {
            top: 230px;
            right: 0;
            max-width: 234px;
        }
    }
}