@import "main-bx";

.attention {
    background-image: url(../img/content/bg2.webp);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    &__inner {
        padding: 80px 0;
        text-align: center;

        @include below(768px) {
            padding: 25px 10px 20px;
        }
    }

    &__title {
        @include fontProperty(700, 30px, 36px, $white);
        text-transform: uppercase;
        margin-bottom: 35px;

        @include below(768px) {
            font-size: 20px;
            line-height: 24px;
            margin-bottom: 20px;
            padding: 0 20px;
        }
    }

    &__text {
        @include fontProperty(400, 24px, 38px, $white);
        margin-bottom: 42px;

        @include below(768px) {
            font-size: 16px;
            line-height: 25px;
            margin-bottom: 20px;
            padding: 0 20px;
        }
    }

    &__form {
        .form__label {
            margin-right: 20px;
            @include below(768px) {
                margin-right: 0;
                width: 100%;
                margin-bottom: 10px;
                display: block;
            }
            input {
                background-color: $white;
                color: $black;
                border-color: $white;

                @include below(768px) {
                    width: 100%;
                }
            }
        }
        
        .btn {
            padding: 12px 54px;
        }
    }
}